export default {
	data() {
		return {
			alertBoxIsShow: false,
			allPic: 0, //全部价格
			shopId: [], //所有商品id
			delId: 0, //删除ID
			shopCount: 0, //全部商品数量
			isAllChecked: false,
			shopList: [
				/* {
										"name": "周杰伦",
										"pic": 0.1,
										"micName": "黑色毛衣",
										"num": 1,
										"id": "1",
										"isChecked": false,
									},
									{
										"name": "华晨宇",
										"pic": 0.1,
										"micName": "烟火里的尘埃",
										"num": 2,
										"id": "2",
										"isChecked": false,
									} */
			],
		}
	},
	onShow() {

		var shopList = [];
		// 获取购物车数据
		shopList = wx.getStorageSync('addShowList');

		let arr = [];
		var shopnum = 0;
		if (shopList.length > 0) {
			shopList.map((item) => {
				arr.push(item.id);
				shopnum += item.num;
			})
		}
		this.shopCount = shopnum;
		this.shopId = arr;
		this.shopList = shopList;

	},
	methods: {
		// 去订单页
		goConfirmation() {
			if (this.allPic > 0) {
				uni.navigateTo({
					url: "/pages/shopConfirmation/shopConfirmation"
				})
			} else {
				wx.showToast({
					title: '请选择商品',
					image: '/static/img/login_close.png',
					duration: 2000
				})
			}

		},
		//加减合并
		addBtn(e, type, idx) {
			let list = this.shopList;
			let allPic = this.allPic;
			list.map((item) => {
				if (item.id == e) {
					if (type == "add") {
						console.log("add")
						item.num++;
						this.shopCount++;
						if (item.isChecked) {
							this.allPic = Number((item.pic + allPic).toFixed(2));
						}
					} else {
						console.log("减")
						if (item.num == 1) {
							this.delId = idx;
							this.alertBoxIsShow = true
						} else {
							item.num--;
							this.shopCount--;
							if (item.isChecked) {
								this.allPic = Number((allPic - item.pic).toFixed(2));
							}
						}

					}

				}
			})
			this.shopList = list
		},
		// 全选
		allChecken() {
			if (this.isAllChecked) {
				// 取消全选
				let list = this.shopList;
				this.isAllChecked = false;
				this.allPic = 0;
				list.map((item) => {
					item.isChecked = false
				})

			} else {
				// 全选
				let list = this.shopList;
				this.isAllChecked = true;
				let allPic = 0;
				list.map((item) => {
					item.isChecked = true
					allPic += parseFloat(item.pic) * item.num;
				})
				this.list = list
				this.allPic = parseFloat(allPic.toFixed(2));
			}

		},
		// 单选中
		checkedBtn(e) {
			let list = this.shopList;
			let allPic = this.allPic;
			let checkedArr = [];
			list.map((item) => {
				if (item.id == e) {
					item.isChecked = !item.isChecked
					if (item.isChecked) {
						this.allPic = Number((item.pic * item.num + allPic).toFixed(2));
					} else {
						console.log("未选择")
						console.log(item.pic * item.num)
						console.log(allPic)
						this.allPic = Number((allPic - item.pic * item.num).toFixed(2));
					}
				}
				// 判断全选
				if (item.isChecked) {
					checkedArr.push(item.id)
				}

			})
			if (this.shopId.length == checkedArr.length) {
				this.isAllChecked = true
			} else {
				this.isAllChecked = false
			}
			this.shopList = list
		},
		// 浮层关闭
		alertBoxClose() {
			this.alertBoxIsShow = false
		},
		// 删除
		alertBoxDelete(e) {
			let delId = this.delId;
			console.log(delId);
			this.shopList.splice(delId, 1);
			this.shopCount--;
			this.alertBoxIsShow = false
			// this.shopId.splice(delId, 1);
		},
		strobtn() {
			uni.switchTab({
				url: '../index/index',
			})
		}
	}
}
